.header {
    position: fixed;
    width: 100%;
    z-index: 9999999999999;
}

.header__wrapper {
    padding: 20px 25px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    z-index: 9999999999999;
    position: relative;
}

.header__logo {
    height: 35px;
    width: auto;
}

.menu {
    max-width: 100%;
    width: 700px;
    height: 100vh;
    position: fixed;
    top: 0;
    right: 0;
    color: #333;
    /*background-color: rgba(0, 0, 0, 0.8);*/
    background-color: white;
    z-index: 3;
    -webkit-transition: 0.8s;
    -o-transition: 0.8s;
    transition: 0.8s;


    overflow: scroll;
    -webkit-box-shadow: 0px 0px 31px 0px rgba(0, 0, 0, 0.35);
    box-shadow: 0px 0px 31px 0px rgba(0, 0, 0, 0.35);
    opacity: 1;
}

.menu__closed {
    right: -100vw;
    -webkit-transition: 0.8s;
    -o-transition: 0.8s;
    transition: 0.8s;
    opacity: 0;
}

.menu__column {
    padding-left: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 300px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.hamburger {
    padding: 0;
    border: none;
    outline: none;
}

.menu__h4__prefix {
    position: absolute;
    left: 0;
    font-size: 0.8em;
    bottom: 0;
    color: #777;
}

.menu__h3 {
    margin-top: 30px;
    margin-bottom: 10px;
    color: #219ed6;
}
.menu__adress{
    margin-bottom: 0;
}
.menu__h4 {
    margin-top: 5px;
    margin-bottom: 15px;
    position: relative;
}

.phone__with_add {
    position: relative;
}

.menu__first__tel {
    margin-bottom: 10px;
    position: relative;
}

.menu__link {
    color: inherit;
    margin-top: 5px;
    text-decoration: none;
    font-size: 0.85em;
    margin-left: 10px;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}

.menu__link:hover {
    color: #0c4159;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    text-decoration: underline;
}

.hamburger-inner,
.hamburger-inner:after,
.hamburger-inner:before {
    position: absolute;
    width: 30px !important;
    height: 3px !important;
    -webkit-transition-timing-function: ease;
    -o-transition-timing-function: ease;
    transition-timing-function: ease;
    -webkit-transition-duration: .15s;
    -o-transition-duration: .15s;
    transition-duration: .15s;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;

    background-color: #000;
    background-color: #219ed6 !important;
}

.hamburger--arrowalt.is-active .hamburger-inner:before {
    top: 0;
    -webkit-transition: top .1s ease, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: top .1s ease, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    -o-transition: top .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: top .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: top .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    -webkit-transform: translate3d(-8px, -10px, 0) rotate(-45deg) scaleX(.7);
    transform: translate3d(8px, -5px, 0) rotate(35deg) scaleX(.5) !important;
}

.hamburger--arrowalt.is-active .hamburger-inner:after {
    bottom: 0;
    -webkit-transition: bottom .1s ease, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: bottom .1s ease, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    -o-transition: bottom .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: bottom .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    transition: bottom .1s ease, transform .1s cubic-bezier(.895, .03, .685, .22) .1s, -webkit-transform .1s cubic-bezier(.895, .03, .685, .22) .1s;
    -webkit-transform: translate3d(-8px, 10px, 0) rotate(45deg) scaleX(.7);
    transform: translate3d(8px, 5px, 0) rotate(-35deg) scaleX(.5) !important;
}

.fa-1-3 {
    font-size: 1.1em;
}



.menu__socials {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 200px;
}

.menu__socials i:hover {
    color: #219ed6;
}

.manuWrap {
    margin-top: 75px;
    
    display: -ms-flexbox;
    
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    min-height: calc(100% - 75px)
}

.menu__adress,.menu__email {
    font-size: 0.85em;
}
.menu__email{
    margin-top: 2px;
}
.menu__socials,
.menu__h4__second,
.phone__with_add,
.menu__adress,
.menu__email {
    margin-left: 10px;
}

.menu__socials i {
    color: #777;
}
.menu__email{
    margin-top: 15px;
}
.menu__link__2{
    margin:0;
    margin-left: 10px;
    color: #777;
}
@media only screen and (max-width:850px) {
  .header__open .header__wrapper{
    background-color: #fff;
    -webkit-transition: 2s;
    -o-transition: 2s;
    transition: 2s;

  }
}


